.body {
  height: 100vh;
  margin-top: 92px;
  background-image: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%);
  display: flex;
  justify-content: center;
}
.content {
  width: 50%;
  display: flex;
  flex-direction: column;
  background-color: var(--white);

  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  padding: 10px;
  margin: 20px;
  border-radius: 5px;
}

.header {
  text-align: center;
  margin: 10px 0;
  h2 {
    color: var(--primary);
  }
}
.info {
  margin: 10px 0;
  color: var(--primary);
  h3 {
    font-weight: 600;
  }
}

.payment {
  margin: 10px 0;
  color: var(--primary);
  h3 {
    font-weight: 600;
  }
}
